export default function Menu({ login }) {
  return (
    <section class="menu mt-5" id="menu">
      <div class="container">
        <div class="display-5 text-secondary text-uppercase text-center pb-3">
           
        </div>
        {daysOfWeek.map(({ text, path, isLink, slides }) => (
          <>
            <div class="d-flex justify-content-center align-items-center">
              {isLink ? (
                login ? (
                  <Link
                    to={path}
                    class="h3 text-danger text-center fw-600 link text-nowrap link d-flex flex-nowrap align-items-center"
                  >
                    {text}
                    <img
                      src={arrowRight}
                      class="ms-2 mt-1"
                      width="50"
                      height="50"
                      alt="Arrow right"
                    />
                  </Link>
                ) : (
                  <div
                    role="button"
                    class="h3 text-danger text-center fw-600 link text-nowrap link d-flex flex-nowrap align-items-center"
                    data-bs-toggle="modal"
                    data-bs-target="#modal"
                  >
                    {text}
                    <img
                      src={arrowRight}
                      class="ms-2 mt-1"
                      width="50"
                      height="50"
                      alt="Arrow right"
                    />
                  </div>
                )
              ) : (
                <div class="h2 text-danger text-center fw-600 text-nowrap d-flex flex-nowrap align-items-center">
                  {text}
                </div>
              )}
            </div>
            <Swiper
              pagination={window.innerWidth < 576}
              modules={[Pagination]}
              spaceBetween={50}
              slidesPerView={"auto"}
            >
              {slides.map(({ img, price, weight, title, text }) => (
                <SwiperSlide>
                  <div
                    class="card"
                    style={{ borderRadius: "25px", minHeight: "600px" }}
                  >
                    <img
                      src={img}
                      class="card-img-top"
                      alt=""
                      style={{ borderRadius: "25px" }}
                    />
                    <div class="card-body">
                      <h5 class="card-title fw-bold d-flex justify-content-between">
                        {title} <span className="text-nowrap">{price} </span>
                      </h5>
                      <p class="card-text text-secondary fw-bold">{text}</p>
                      <div class="fw-bold">{weight} </div>
                    </div>
                  </div>
                </SwiperSlide>
              ))}
            </Swiper>
          </>
        ))}
      </div>
    </section>
  );
}
