import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';
import { saveAs } from 'file-saver';
import { getDatabase, ref, get } from 'firebase/database';

const AdminPage = (props) => {
  const [orders, setOrders] = useState({});

  const today = new Date();
  let tomorrow = new Date(today);
  tomorrow.setDate(today.getDate() + 1);
  const formattedDate = tomorrow.toLocaleDateString('en-GB').replace(/[^\d]/g, '');

  const path = "orders/" + formattedDate;

  const database = getDatabase();

  const dataRef = ref(database, path);

  useEffect(() => {
    get(dataRef)
    .then((snapshot) => {
      if (snapshot.exists()) {
        const jsonData = snapshot.val();
        setOrders(jsonData)
      } else {
        console.log('No data available');
      }
    })
    .catch((error) => {
      console.error('Error fetching data:', error);
    });
  }, [])

  const downloadOrders = () => {
    const data = [];

    Object.entries(orders).forEach(([date, logins]) => 
      Object.entries(logins).forEach(([login, names]) => 
        Object.entries(names).forEach(([name, order]) => {
          data.push({
            [""]: login,
            ["'"]: order.name.data,
            [""]: order.first.data ? '' : '',
            [""]: order.second.data ? '' : '',
            ["  + "]: order.third.data ? '' : '',
            [" "]: order.addFirst.data ? '' : '',
            ["       \"\""]: order.addSecond.data ? '' : '',
            [" \"\"  "]: order.addThird.data ? '' : '',
            [" \"\"    "]: order.addFourth.data ? '' : '',
            [" "]: order.fourth.data ? '' : '',
          });
        })
      )
    );

    const csv = Papa.unparse(data);
    const blob = new Blob([csv], {type: "text/plain;charset=utf-8"});
    saveAs(blob, "orders.csv");
  }

  return (
    <div className="container">
      <h2 className="my-4"> </h2>
      <table className="table table-striped">
        <thead>
          <tr>
            <th></th>
            <th>'</th>
            <th></th>
            <th></th>
            <th>  + </th>
            <th> </th>
            <th>       ""</th>
            <th> ""  </th>
            <th> ""    </th>
            <th> </th>
          </tr>
        </thead>
        <tbody>
          {Object.entries(orders).map(([login, names]) => 
              Object.entries(names).map(([name, order], index) => (
                <tr key={index}>
                  <td>{login}</td>
                  <td>{order.name.data}</td>
                  <td>{order.first.data ? '' : ''}</td>
                  <td>{order.second.data ? '' : ''}</td>
                  <td>{order.third.data ? '' : ''}</td>
                  <td>{order.addFirst.data ? '' : ''}</td>
                  <td>{order.addSecond.data ? '' : ''}</td>
                  <td>{order.addThird.data ? '' : ''}</td>
                  <td>{order.addFourth.data ? '' : ''}</td>
                  <td>{order.fourth.data ? '' : ''}</td>
                </tr>
              ))
            )}
        </tbody>
      </table>
      <button className="btn btn-primary" onClick={downloadOrders}></button>
    </div>
  );
}

export default AdminPage;
